

.MContact{
    height: 248vh;
    width: 100%;
    display: flex;
    flex-direction: column;
    /* background-color: pink; */
}

.Psection1{
    height: 60vh;
    width: 100%;
    margin-top: 65px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: lightgrey;
    /* background-color: pink; */
}

.Psection1 .sub1{
    height: 20vh;
    width: 100%;
    /* background-color: lightskyblue; */
    display: flex;
    align-items: center;
    justify-content: center;
}
.Psection1 .sub1>h1{
    /* font-size: var(--h1-style); */
    font-size: 40px;
    font-family: var(--h1-style);
}
.Psection1 .sub2{
    height: 40vh;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-around;
    /* background-color: lightseagreen; */
}

.Psection1 .sub2 .scard{
    height: 40vh;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    justify-content: flex-start;
    flex-direction: column;
    /* background-color: rgb(55, 255, 88); */
}

.Psection1 .sub2 .scard > h1{
    font-size: 40px;
    padding-bottom: 20px;
    font-weight: 700;
    line-height: 30px;
    color: white;
    font-family: var(--h1-style);
}

.Psection1 .sub2 .scard > p{
    font-size: 20px;
    /* padding: 10px; */
    font-weight: 500;
    line-height: 25px;
    font-family: var(--p-style);

}

.Psection1 .sub2 .sub__3 > h2{
    padding-bottom: 10px;
    font-size: 25px;
    font-weight: 700;
    font-family: var(--p-style);

}

.Psection1 .sub3 {
    height: 40vh;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    background-color: rgb(0, 170, 170);
}

.Psection1 .sub3 > h1{
    color: white;
    font-size: 45px;
    padding: 20px;
}

.Psection1 .sub3 > p{
    color: rgb(228, 228, 228);
    font-size: 30px;
    padding: 10px;
    font-weight: 500px;
}

/* ------------------- section 2 --------------------- */

