.Psection2 > form .Submit{
    /* background-color: rgb(38, 139, 255); */
    background-color: black;
    color: white;
    font-size: 16px;
}

.Psection2 .input{
    /* width: 400px; */
    /* font-family: var(--p-style); */
}


/* ----------------- section 3 --------------- */


.Contact {
    height: 50vh;
    width: 100%;
    white-space: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: black;
}

.Contact__sub1 {
    display: flex;
    flex-direction: column;
    /* justify-content: center;
    align-items: center; */
}

.Contact__sub1>h6 {
    font-size: 16px;
    color: white;
    letter-spacing: 1.7px;
    color: orangered;
    width: 100%;
    color: rgb(32, 182, 154);
    /* margin: 20px 0 0 30px; */
    margin: 10px 50px;
}

.Contact__sub1>h1 {
    font-size: 60px;
    font-weight: 800;
    padding: 10px;
    color: white;
    margin: 10px 50px;
    font-family: var(--h1-style);
    /* color: black; */
    /* filter: drop-shadow(0.35rem 0.35rem 0.4rem rgb(252, 252, 252)); */
}

.Contact__sub1>h1>span {
    font-size: 60px;
    font-weight: 800;
    color: white;
    color: rgb(0, 128, 104);
    color: #c4c4c4;
    color: black;
    /* margin: 20px; */
    font-family: var(--h1-style);

    text-shadow: -2px -2px 3px #FF0000, 2px 2px 3px #0000FF;
    text-shadow: 2px -2px 3px #FF0000, -2px 2px 3px #0000FF;
    text-shadow: -1px 1px 0 rgb(255, 255, 255), 1px 1px 0 rgb(255, 255, 255), 1px -1px 0 rgb(255, 255, 255), -1px -1px 0 rgb(255, 255, 255);
}

.Contact__button {
    width: 300px;
    height: 60px;
    font-size: 16px;
    padding: 14px 35px;
    border: 2px solid rgb(32, 182, 154);
    border: 2px solid rgb(32, 182, 154);
    background-color: black;
    color: white;
    transition: 0.5s;
    word-spacing: 4px;
    /* margin: 10px 40px; */
    margin-right: 100px;
    cursor: pointer;
}

.Contact__button:hover {
    /* background-color: rgb(255, 255, 255); */
    border-color: rgb(32, 182, 154);
    border: 2px soild white;
    color: rgb(0, 0, 0);
    color: white;
    color: rgb(32, 182, 154);
}

@media only screen and (max-width: 900px) {
    .Contact {
        height: 40vh;
        width: 100%;
        white-space: 100%;
        display: flex;
        justify-content: space-evenly;
        align-items: center;
        background-color: black;
        flex-direction: column;
    }
    .Contact__sub1 {
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .Contact__sub1>h1 {
        font-size: 30px;
        font-weight: 800;
        color: white;
        padding: 10px;

    }
    .Contact__sub1>h1>span {
        font-size: 30px;
        font-weight: 500;
        color: black;
        margin: 10px;
        text-shadow: -1px 1px 0 rgb(255, 255, 255), 1px 1px 0 rgb(255, 255, 255), 1px -1px 0 rgb(255, 255, 255), -1px -1px 0 rgb(255, 255, 255);
    }
    .Contact__sub1>h6 {
        margin: 20px 0 0 30px;
    }
    .Contact__button {
        width: 200px;
        height: 40px;
        font-size: 16px;
        padding: 5px 12px;
        border: 1px solid white;
        background-color: black;
        color: white;
        transition: 1s;
        word-spacing: 4px;
        margin: 10px;
        border-radius: 50px;
    }
}
